.projects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  background-image: -webkit-linear-gradient(salmon 10%, teal 30%);
  background-image: linear-gradient(salmon 10%, teal 30%);
  min-height: 95vh;
  overflow: hidden;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.projects p:last-child {
  margin-bottom: 0;
}

.project-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 90%;
  background-color: bisque;
  border-radius: 2vmin;
  height: 50vmin;
  margin: 3vmin;
  color: black;
  font-size: calc(5px + 1vmin);
}

.project-card h3 {
  font-weight: bold;
}

.project-card .project-card-thumb {
  min-height: 40vmin;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.project-card a {
  display: block;
  font-size: 1vmax;
  color: black;
}

.project-card a:hover {
  display: block;
  font-size: 1vmax;
  font-weight: bold;
  color: darkred;
  text-decoration: none;
}

@media only screen and (orientation: landscape) {
  .project-card {
    width: 25%;
  }
}

